
/* 헤더 */
#header {
    width: 100%;height: 100px;display: flex;position: relative;z-index: 3;
    border-bottom: 1px solid #eee;
    .left {
        width: 200px;height: 100%;display: flex;align-items: center;
        h1 {
            width:100%;height: 40px;
            a {
                font-size: 24px;
                font-weight: 600;
                display: flex;
                width: 100%;
                height: 100%;
                align-items: center;
                justify-content: center;
                span:nth-child(1) {
                    color: #5ac;
                }
                span:nth-child(2) {
                    margin-left: 5px;
                    color: #c5a;
                }
            }
        }
    }
    .left {
        width: 200px;height: 100%;display: flex;align-items: center;
        h1 {
            width:100%;height: 40px;
            a {
                font-size: 24px;
                font-weight: 600;
                display: flex;
                width: 100%;
                height: 100%;
                align-items: center;
                justify-content: center;
                span:nth-child(1) {
                    color: #5ac;
                }
                span:nth-child(2) {
                    margin-left: 5px;
                    color: #c5a;
                }
            }
        }
    }
    .right {
        width: 1000px;height: 100%;    
        position: relative;
        #aside {
            position: absolute;
            right: 0;
            top: 20px;
            a {
                font-size: 13px;
                transition: .5s;
                &:hover {
                    color: plum;
                }
            }
            i {
                padding: 0 10px;
                font-size: 11px;
                color: #ccc;
            }
        }
        #nav {
            width:700px;
            position: absolute;
            right: 0;
            bottom: 0;
            // margin:49px 20px 0 auto; 
            > ul {
                display: flex;width:100%;
                > li {
                    width:25%;
                    border-top:1px solid #ddd;
                    border-bottom:1px solid #ddd;
                    border-left:1px solid #ddd;
                    &:last-child {
                        border-right:1px solid #ddd;
                    
                    }
                    > a {
                        width: 100%;display: flex;align-items: center;justify-content: center;
                        height: 50px;
                        font-size: 15px;
                        font-weight: 600;
                        color: #000;
                        transition: all 0.3s;
                        background: #fff;
                        &:hover {
                            background: #2c2a29;
                            color: #669900;
                        }  
                        &.on {
                            background: #2c2a29;
                            color: #669900;
                        }    
                    }

                    &:nth-child(1) > a.on {background: #5c2a29;}
                    &:nth-child(3) > a.on {background: #5c2a79;}
                    &:nth-child(4) > a.on {background: #2c5a99;}

                    .sub {
                        display: block;
                        position: absolute;
                        top:100px;
                        left:0;
                        width: 100%;
                        background: #2c2a29;
                        .sub-container {
                            width:700px;
                            margin: 0 20px 0 auto;
                            > ul {
                                width:100%;display: flex;padding: 20px 0;
                                > li {
                                    width:25%;
                                    > ul {
                                        width:100%;
                                        > li {
                                            width:100%;
                                            a {
                                                color:#999;
                                                display: flex;
                                                height: 30px;
                                                width: 100%;
                                                align-items: center;
                                                justify-content: center;
                                                &:hover {
                                                    background: #666;
                                                    color: #fff;
                                                }
                                            }
                                        }
                                    }
                                }
                                     /* 4칸 */
                               
                            }
                        }
                    }
                    .sub1 {background: #5c2a29;}
                    .sub2 {background: #2c5a29;}
                    .sub3 {background: #5c2a79;}
                    .sub4 {background: #2c5a99;}
                }
            }
        }
    }
}









